import React from 'react'

const FromDelivery = () => (
    <div className="jumbotron-sm">
    <div className="container">
    <div className="row">
    <div className="col-md-8">
    <div className="well-sm">
    <form>
    <div className="row">
    <div className="col-md-6">
    <div className="form-group">
    <label for="name">
        Адрес доставки</label>
    <input type="text" className="form-control" id="name" placeholder="улица, квартира" required="required" />
    </div>

    <div className="form-group">
    <label for="email">
    Email</label>
<div className="input-group">
    <span className="input-group-addon"><span className="glyphicon glyphicon-envelope"></span>
    </span>
    <input type="email" className="form-control" id="email" placeholder="your@email.com" required="required" /></div>
    </div>
        <div className="form-group">
            <label for="email">
                Телефон</label>
                <input type="text" id="phone" className="form-control"  placeholder="+79092345678" required="required"/></div>

</div>
<div className="col-md-6">
    <div className="form-group">
    <textarea name="message" id="message" className="form-control" rows="9" cols="25"
placeholder="Пожелания"></textarea>
    </div>
    </div>
    <div className="col-md-12">
    <button type="submit" className="btn btn-primary pull-right" id="btnContactUs">
    Заказать</button>
</div>
</div>
</form>
</div>
</div>
</div>
</div>
</div>
)


export default FromDelivery



